<template>
  <div id="map">
    <div class="setting">
      <div class="slider">
        <span>radius size</span>
        <el-slider v-model="state.radiusSize" @input="handleRadiusChange" />
      </div>
      <div class="slider">
        <span>blur size</span>
        <el-slider v-model="state.blurSize" @input="handleBlurChange" />
      </div>
    </div>
  </div>
</template>

<script setup>
  import "ol/ol.css";
  import { onMounted, reactive } from "vue";
  import { Map, View } from "ol";
  import { Tile as TileLayer, Heatmap } from "ol/layer";
  import { defaults, FullScreen } from "ol/control";
  import { XYZ, Vector as VectorSource } from "ol/source";
  import { KML } from "ol/format";
  import { MAPURL, ATTRIBUTIONS } from "@/constants";
  import updateMapSize from "@/hooks/updateMapSize";
  const state = reactive({
    radiusSize: 10,
    blurSize: 15,
  });
  //Heatmap
  const vector = new Heatmap({
    //KML
    source: new VectorSource({
      url: "data/kml/2012_Earthquakes_Mag5.kml",
      format: new KML({
        extractStyles: false,
      }),
      wrapX: false,
    }),
    //
    radius: parseInt(state.radiusSize, 10),
    //
    blur: parseInt(state.blurSize, 10),
  });
  //addfeature
  vector.getSource().on("addfeature", (event) => {
    // 2012_Earthquakes_Mag5.kml
    //
    const name = event.feature.get("name");
    //magnitude
    const magnitude = parseFloat(name.substr(2));
    //weight
    event.feature.set("weight", magnitude - 5);
  });
  const raster = new TileLayer({
    source: new XYZ({
      attributions: ATTRIBUTIONS,
      url: MAPURL,
      maxZoom: 20,
    }),
  });
  const initMap = () => {
    const map = new Map({
      //map
      target: "map",
      //
      layers: [
        //
        raster,
        vector,
      ],
      view: new View({
        projection: "EPSG:4326", // EPSG:4326EPSG:3 857
        center: [0, 0],
        //
        minZoom: 2,
        zoom: 2,
      }),
      //
      controls: defaults().extend([
        new FullScreen(), //IE
      ]),
    });
    // 
    updateMapSize(map);
  };
  // 
  const handleRadiusChange = () => {
    //
    vector.setRadius(parseInt(state.radiusSize, 10));
  };
  // 
  const handleBlurChange = () => {
    //
    vector.setBlur(parseInt(state.blurSize, 10));
  };
  onMounted(() => {
    initMap();
  });
</script>

